import {Injectable} from '@angular/core';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';

import {HttpClient, HttpErrorResponse} from '@angular/common/http';
import { Http, Response, Headers, RequestOptions, RequestMethod } from '@angular/http';

import { ANIMATE_ON_ROUTE_ENTER } from '@app/core';
import { Employee } from '../../static/models/employee';

import { AppComponent } from '../../app.component';
import 'rxjs/add/operator/map';


@Injectable()
export class DataService {
  private readonly API_URL = 'http://127.0.0.1:8000/api/employees/';

  dataChange: BehaviorSubject<Employee[]> = new BehaviorSubject<Employee[]>([]);
  // Temporarily stores data from dialogs
  dialogData: any;

  selectedEmployee : Employee;
  employeeList : Employee[];

  constructor (private http: Http) {}

 
  get data(): Employee[] {
    return this.dataChange.value;
  }

  getDialogData() {
    return this.dialogData;
  }

  /** CRUD METHODS */
  getEmployeeList(){
    this.http.get('http://127.0.0.1:8000/api/employees/')
    
    .map((data : Response) =>{
      return data.json() as Employee[];
    }).toPromise().then(x => {
      this.employeeList = x;
      console.log(this.employeeList);
    })
  }
 


    // ADD, POST METHOD
    postEmployee(emp : Employee){
      var body = JSON.stringify(emp);
      var headerOptions = new Headers({'Content-Type':'application/json'});
      var requestOptions = new RequestOptions({method : RequestMethod.Post,headers : headerOptions});
     
      return this.http.post('http://127.0.0.1:8000/api/employees/create/',body,requestOptions).map(x => x.json());
    }
  
      // UPDATE, PUT METHOD
      putEmployee(id, emp) {
        var body = JSON.stringify(emp);
        var headerOptions = new Headers({ 'Content-Type': 'application/json' });
        var requestOptions = new RequestOptions({ method: RequestMethod.Put, headers: headerOptions });
        return this.http.put('http://127.0.0.1:8000/api/employees/' + id + '/edit/',
          body,
          requestOptions).map(res => res.json());
      }
  
    // DELETE METHOD
    deleteEmployee(id: number) {
          return this.http.delete('http://127.0.0.1:8000/api/employees/' + id + '/delete/').map(res => res.json());
        }



}
















